@import './../../index.css';

.home--container{
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--black);
  user-select: none;
}

.logout--button--container{
  min-width: 100%;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: var(--violet-black);
}

.logout--button{
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  background-color: var(--violet-black);
  color: var(--white);
  
  transition: all 0.2s ease-in-out;
}

.logout--button:hover{
  transform: scale(1.5,1.5);
  cursor: pointer;
}